.card-forget {
                         display: flex;
                         justify-content: center;
                         width:30%;
                         margin:0 auto;
                         position:absolute;
                         top:30%;
                         left: 35%;
                         -ms-transform: translateY(-50%);
                         transform: translateY(-50%,-50%);

}
.card-forgetPassword {
                         box-shadow: 0 20px 20px rgba(0,0,0,0.2),
                         0px 0px 50px rgba(0,0,0,0.2);
}

.forgetpassword {
                         display:flex;
                         flex-direction: column;
                         gap:8px;
                         width:350px;
                         height:min-content;    

}
